'use client'

import { useState, useRef, useEffect } from 'react'
import tutorialsData from '@/app/tutoriallibrary/tutorialData'
import { useRouter } from 'next/navigation'

interface TutorialStep {
  text: string
  image?: string
}

interface Tutorial {
  id: string
  title: string
  description?: string
  url?: string
  category?: string
  steps?: TutorialStep[]
}

interface Message {
  id: string
  text: string
  sender: 'user' | 'assistant'
  type?: 'text' | 'tutorial'
  tutorialId?: string
  timestamp: Date
}

export default function FloatingChatbot() {
  const router = useRouter()
  const [isOpen, setIsOpen] = useState(false)
  const [messages, setMessages] = useState<Message[]>(() => {
    const saved = localStorage.getItem('chat_history')
    return saved
      ? JSON.parse(saved)
      : [
          {
            id: '1',
            text: "Hello! I'm your ElderEase assistant. How can I help you with social media today?",
            sender: 'assistant',
            type: 'text',
            timestamp: new Date()
          }
        ]
  })
  const [inputText, setInputText] = useState('')
  const messagesEndRef = useRef<HTMLDivElement>(null)

  // Scroll to bottom and save history
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
    localStorage.setItem('chat_history', JSON.stringify(messages))
  }, [messages])

  // Simple social media Q&A
  const simpleAnswers: { [key: string]: string } = {
    'how to post on facebook': 'To post on Facebook, open the app, click "Whatâ€™s on your mind?", type your message or add a photo/video, then click "Post".',
    'how to like a post on facebook': 'Tap the "Like" button under the post you want to like.',
    'how to comment on facebook': 'Tap "Comment" under the post, type your comment, then press "Send".',
    'how to send photos on whatsapp': 'Open a chat, tap the attachment icon, select "Gallery" or "Camera", choose a photo, then tap the send button.',
    'how to add a contact on whatsapp': 'Go to WhatsApp settings > Contacts > Add New Contact, then enter their details.',
    'how to video call on whatsapp': 'Open a chat with the person, then tap the video call icon at the top right.',
    'what is instagram': 'Instagram is a social media platform where you can share photos, videos, and stories with friends and followers.',
    'make my account safe': 'Use strong passwords, enable two-factor authentication, and be careful with unknown links or messages.'
  }

  // --- Conversational AI Logic ---
  const answerQuery = (text: string): Message[] => {
    const lower = text.toLowerCase()
    const replies: Message[] = []

    // 1. Check for simple Q&A match
    const answer = Object.entries(simpleAnswers).find(([key]) => lower.includes(key))
    if (answer) {
      replies.push({
        id: Date.now().toString(),
        text: answer[1],
        sender: 'assistant',
        type: 'text',
        timestamp: new Date()
      })
      return replies
    }

    // 2. Otherwise, find tutorial dynamically
    const matchedTutorial = tutorialsData.find(
      t =>
        t.title.toLowerCase().includes(lower) ||
        t.category?.toLowerCase().includes(lower)
    )

    if (matchedTutorial) {
      replies.push({
        id: Date.now().toString(),
        text: `I can guide you through ${matchedTutorial.title}.`,
        sender: 'assistant',
        type: 'tutorial',
        tutorialId: matchedTutorial.id,
        timestamp: new Date()
      })
    } else {
      replies.push({
        id: Date.now().toString(),
        text: "Sorry, I didn't understand. Try asking about social media tutorials like Facebook, WhatsApp, or Instagram.",
        sender: 'assistant',
        type: 'text',
        timestamp: new Date()
      })
    }

    return replies
  }

  const handleSend = () => {
    if (!inputText.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputText,
      sender: 'user',
      type: 'text',
      timestamp: new Date()
    }

    setMessages(prev => [...prev, userMessage])
    setInputText('')

    const replies = answerQuery(inputText)
    setTimeout(() => setMessages(prev => [...prev, ...replies]), 500)
  }

  const goToTutorial = (tutorialId?: string) => {
    if (!tutorialId) return
    const tutorial = tutorialsData.find(t => t.id === tutorialId)
    if (tutorial) router.push(`/tutoriallibrary#${tutorial.id}`)
  }

  const quickQuestions = [
    'How to post on Facebook?',
    'Send photos on WhatsApp',
    'Video call my family',
    'What is Instagram?',
    'Make my account safe'
  ]

  return (
    <>
      {/* Floating Chat Button */}
      {!isOpen && (
        <button
          onClick={() => setIsOpen(true)}
          className="fixed bottom-6 right-6 w-16 h-16 bg-blue-600 hover:bg-blue-700 rounded-full shadow-2xl flex items-center justify-center text-white transition-all duration-300 hover:scale-110 z-50"
        >
          ðŸ¤–
        </button>
      )}

      {/* Chat Modal */}
      {isOpen && (
        <div className="fixed bottom-6 right-6 w-96 h-[600px] bg-white rounded-2xl shadow-2xl flex flex-col z-50 border border-gray-200">
          {/* Header */}
          <div className="bg-blue-600 text-white p-4 rounded-t-2xl flex justify-between items-center">
            <h3 className="font-semibold text-lg">ElderEase Assistant</h3>
            <button onClick={() => setIsOpen(false)} className="text-white text-2xl">
              Ã—
            </button>
          </div>

          {/* Messages */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4">
            {messages.map(msg => (
              <div
                key={msg.id}
                className={`flex ${msg.sender === 'user' ? 'justify-end' : 'justify-start'}`}
              >
                <div
                  className={`max-w-[80%] rounded-2xl p-3 ${
                    msg.sender === 'user'
                      ? 'bg-blue-600 text-white rounded-br-none'
                      : 'bg-gray-100 text-gray-800 rounded-bl-none'
                  }`}
                >
                  <div className="whitespace-pre-wrap text-sm">{msg.text}</div>

                  {/* Tutorial Steps */}
                  {msg.type === 'tutorial' && msg.tutorialId && (() => {
                    const tutorial = tutorialsData.find(t => t.id === msg.tutorialId)
                    if (!tutorial)
                      return <div className="text-xs text-red-500 mt-1">Tutorial not found.</div>

                    return (
                      <div className="mt-2 space-y-2">
                        {tutorial.steps?.map((step, index) => (
                          <div
                            key={index}
                            className="bg-gray-50 p-2 rounded text-gray-800 text-xs"
                          >
                            <strong>Step {index + 1}:</strong> {step.text}
                            {step.image && (
                              <img
                                src={step.image}
                                alt={`Step ${index + 1}`}
                                className="mt-1 rounded max-h-40 w-full object-cover"
                              />
                            )}
                          </div>
                        ))}
                        <button
                          onClick={() => goToTutorial(msg.tutorialId)}
                          className="mt-2 px-2 py-1 bg-green-500 text-white rounded hover:bg-green-600 text-xs"
                        >
                          Show me how
                        </button>
                      </div>
                    )
                  })()}

                  <div
                    className={`text-xs mt-1 ${
                      msg.sender === 'user' ? 'text-blue-200' : 'text-gray-500'
                    }`}
                  >
                    {new Date(msg.timestamp).toLocaleTimeString([], {
                      hour: '2-digit',
                      minute: '2-digit'
                    })}
                  </div>
                </div>
              </div>
            ))}
            <div ref={messagesEndRef} />
          </div>

          {/* Quick Questions */}
          <div className="border-t border-gray-200 p-3">
            <p className="text-xs text-gray-600 mb-2">Quick questions:</p>
            <div className="flex flex-wrap gap-1">
              {quickQuestions.map((q, i) => (
                <button
                  key={i}
                  onClick={() => {
                    setInputText(q)
                    setTimeout(handleSend, 100)
                  }}
                  className="bg-blue-50 text-blue-700 px-2 py-1 rounded-full text-xs hover:bg-blue-100 transition"
                >
                  {q}
                </button>
              ))}
            </div>
          </div>

          {/* Input */}
          <div className="border-t border-gray-200 p-4 flex space-x-2">
            <input
              type="text"
              value={inputText}
              onChange={e => setInputText(e.target.value)}
              onKeyPress={e => e.key === 'Enter' && handleSend()}
              placeholder="Ask about social media..."
              className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-300 text-sm"
            />
            <button
              onClick={handleSend}
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 disabled:opacity-50 text-sm"
            >
              Send
            </button>
          </div>
        </div>
      )}
    </>
  )
}
